'use client';

import React, { useState, useEffect, useRef } from 'react';

// Swiper's CSS and JS are now loaded via a CDN inside a useEffect hook.
// This is necessary for the code to compile in this environment.

// Prepare the image data. Assuming your images are in the `public` directory.
const posters = Array.from({ length: 14 }, (_, i) => ({
  src: `/poster${i + 1}.jpg`,
  alt: `Poster ${i + 1}`,
}));

// A reusable navigation button component
const NavButton = ({ direction, onClick, disabled }) => (
  <button
    onClick={onClick}
    disabled={disabled}
    className={`absolute top-1/2 -translate-y-1/2 p-2 rounded-full bg-white bg-opacity-70 text-gray-800 shadow-lg hover:bg-opacity-100 transition z-10 ${
      disabled ? 'opacity-50 cursor-not-allowed' : ''
    }`}
    style={{ [direction]: '1rem' }}
  >
    <svg className="w-6 h-6" fill="currentColor" viewBox="0 0 20 20">
      {direction === 'left' ? (
        <path fillRule="evenodd" d="M12.707 5.293a1 1 0 010 1.414L9.414 10l3.293 3.293a1 1 0 01-1.414 1.414l-4-4a1 1 0 010-1.414l4-4a1 1 0 011.414 0z" clipRule="evenodd"></path>
      ) : (
        <path fillRule="evenodd" d="M7.293 14.707a1 1 0 010-1.414L10.586 10 7.293 6.707a1 1 0 011.414-1.414l4 4a1 1 0 010 1.414l-4 4a1 1 0 01-1.414 0z" clipRule="evenodd"></path>
      )}
    </svg>
  </button>
);

// A reusable indicator dots component
const Dots = ({ count, currentIndex, onDotClick }) => (
  <div className="absolute bottom-4 left-1/2 -translate-x-1/2 flex space-x-2 z-10">
    {Array.from({ length: count }).map((_, index) => (
      <button
        key={index}
        onClick={() => onDotClick(index)}
        className={`w-3 h-3 rounded-full transition duration-300 ${
          index === currentIndex ? 'bg-white bg-opacity-100' : 'bg-white bg-opacity-50'
        }`}
      ></button>
    ))}
  </div>
);

// 1. Simple Slider Carousel
const SimpleCarousel = () => {
  const swiperRef = useRef(null);
  const paginationId = React.useId();
  const navigationPrevId = React.useId();
  const navigationNextId = React.useId();

  useEffect(() => {
    const loadSwiper = async () => {
      const Swiper = (await import('https://cdn.jsdelivr.net/npm/swiper@11/swiper-bundle.min.js')).default;
      
      if (swiperRef.current) {
        new Swiper(swiperRef.current, {
          modules: [Swiper.modules.Navigation, Swiper.modules.Pagination],
          loop: true,
          pagination: {
            el: `#${paginationId}`,
            clickable: true,
          },
          navigation: {
            nextEl: `#${navigationNextId}`,
            prevEl: `#${navigationPrevId}`,
          },
        });
      }
    };

    loadSwiper();

  }, [paginationId, navigationNextId, navigationPrevId]);

  return (
    <div className="rounded-2xl shadow-xl p-8 bg-white dark:bg-zinc-800">
      <h2 className="text-3xl font-bold text-center mb-6">Simple Slider</h2>
      <div className="relative rounded-xl shadow-lg">
        <div ref={swiperRef} className="swiper rounded-xl shadow-lg">
          <div className="swiper-wrapper">
            {posters.map((poster, index) => (
              <div key={index} className="swiper-slide">
                <div className="w-full h-96 relative flex items-center justify-center bg-gray-200 dark:bg-zinc-700 rounded-xl">
                  <img
                    src={poster.src}
                    alt={poster.alt}
                    className="p-4 object-contain w-full h-full"
                    loading="lazy"
                  />
                </div>
              </div>
            ))}
          </div>
          <div className="swiper-pagination" id={paginationId}></div>
          <div className="swiper-button-prev" id={navigationPrevId}></div>
          <div className="swiper-button-next" id={navigationNextId}></div>
        </div>
      </div>
    </div>
  );
};

// 2. Fading Carousel
const FadeCarousel = () => {
  const swiperRef = useRef(null);
  const paginationId = React.useId();
  const navigationPrevId = React.useId();
  const navigationNextId = React.useId();

  useEffect(() => {
    const loadSwiper = async () => {
      const Swiper = (await import('https://cdn.jsdelivr.net/npm/swiper@11/swiper-bundle.min.js')).default;
      if (swiperRef.current) {
        new Swiper(swiperRef.current, {
          modules: [Swiper.modules.EffectFade, Swiper.modules.Navigation, Swiper.modules.Pagination],
          effect: 'fade',
          fadeEffect: {
            crossFade: true
          },
          loop: true,
          pagination: {
            el: `#${paginationId}`,
            clickable: true,
          },
          navigation: {
            nextEl: `#${navigationNextId}`,
            prevEl: `#${navigationPrevId}`,
          },
        });
      }
    };
    loadSwiper();
  }, [paginationId, navigationNextId, navigationPrevId]);

  return (
    <div className="rounded-2xl shadow-xl p-8 bg-white dark:bg-zinc-800">
      <h2 className="text-3xl font-bold text-center mb-6">Fading Carousel</h2>
      <div className="relative rounded-xl shadow-lg">
        <div ref={swiperRef} className="swiper rounded-xl shadow-lg">
          <div className="swiper-wrapper">
            {posters.map((poster, index) => (
              <div key={index} className="swiper-slide">
                <div className="w-full h-96 relative flex items-center justify-center bg-gray-200 dark:bg-zinc-700 rounded-xl">
                  <img
                    src={poster.src}
                    alt={poster.alt}
                    className="p-4 object-contain w-full h-full"
                    loading="lazy"
                  />
                </div>
              </div>
            ))}
          </div>
          <div className="swiper-pagination" id={paginationId}></div>
          <div className="swiper-button-prev" id={navigationPrevId}></div>
          <div className="swiper-button-next" id={navigationNextId}></div>
        </div>
      </div>
    </div>
  );
};

// 3. Thumbs Carousel
const ThumbsCarousel = () => {
  const [thumbsSwiper, setThumbsSwiper] = useState(null);
  const swiperMainRef = useRef(null);
  const swiperThumbsRef = useRef(null);
  
  useEffect(() => {
    const loadSwiper = async () => {
      const Swiper = (await import('https://cdn.jsdelivr.net/npm/swiper@11/swiper-bundle.min.js')).default;
      
      if (swiperMainRef.current && swiperThumbsRef.current) {
        const swiperThumbs = new Swiper(swiperThumbsRef.current, {
          spaceBetween: 10,
          slidesPerView: 4,
          freeMode: true,
          watchSlidesProgress: true,
          modules: [Swiper.modules.FreeMode, Swiper.modules.Navigation, Swiper.modules.Thumbs],
        });
        
        new Swiper(swiperMainRef.current, {
          spaceBetween: 10,
          navigation: true,
          thumbs: { swiper: swiperThumbs },
          modules: [Swiper.modules.FreeMode, Swiper.modules.Navigation, Swiper.modules.Thumbs],
        });
      }
    };
    
    loadSwiper();
    
  }, []);

  return (
    <div className="rounded-2xl shadow-xl p-8 bg-white dark:bg-zinc-800">
      <h2 className="text-3xl font-bold text-center mb-6">Thumbs Carousel</h2>
      
      {/* Main Swiper */}
      <div ref={swiperMainRef} className="swiper mySwiper2 rounded-xl shadow-lg mb-4">
        <div className="swiper-wrapper">
          {posters.map((poster, index) => (
            <div key={index} className="swiper-slide">
              <div className="w-full h-96 relative flex items-center justify-center bg-gray-200 dark:bg-zinc-700 rounded-xl">
                <img
                  src={poster.src}
                  alt={poster.alt}
                  className="p-4 object-contain w-full h-full"
                  loading="lazy"
                />
              </div>
            </div>
          ))}
        </div>
      </div>

      {/* Thumbs Swiper */}
      <div ref={swiperThumbsRef} className="swiper mySwiper">
        <div className="swiper-wrapper">
          {posters.map((poster, index) => (
            <div key={index} className="swiper-slide">
              <div className="relative w-full h-24 cursor-pointer opacity-70 hover:opacity-100 transition-opacity">
                <img
                  src={poster.src}
                  alt={`Thumbnail ${index + 1}`}
                  className="rounded-md object-contain w-full h-full"
                  loading="lazy"
                />
              </div>
            </div>
          ))}
        </div>
      </div>
    </div>
  );
};

// Main page component to render all carousels
export default function Home() {
  return (
    <div className="font-sans min-h-screen p-8 pb-20 gap-16 sm:p-20 bg-gray-100 dark:bg-zinc-900 flex flex-col items-center">
      <main className="flex flex-col gap-12 w-full">
        <h1 className="text-5xl font-bold text-center text-gray-900 dark:text-gray-50">
          Swiper.js Carousels
        </h1>
        
        <SimpleCarousel />
        
        <FadeCarousel />
        
        <ThumbsCarousel />
      </main>
      
      <footer className="w-full row-start-3 flex gap-6 flex-wrap items-center justify-center">
        {/* Your existing footer content can go here */}
      </footer>
    </div>
  );
}
